import { createApi, fakeBaseQuery } from "@reduxjs/toolkit/query/react";
import { Usuario, Inventario } from "../models/types";
import { data as databaseData } from  "./mockData";
import { status } from "nprogress";

export const baseApi = createApi({
  reducerPath: 'baseApi',
  baseQuery: fakeBaseQuery(),
  tagTypes: ["User", "Inventory"],
  endpoints: (builder) => ({
    createInventory: builder.mutation<PostResponse, { data: any }>({      
      async queryFn({ data }) {
        try {
          const emptyInventory = {
            id: 0,
            id_usuario: 1,
            data_criacao: new Date().toLocaleDateString('en-CA'),
            data_atualizacao: new Date().toLocaleDateString('en-CA'),
            data_remocao: null,
            calculos: {},
            cliente: { nome: '' },
            decujus: {
              nome: '',
              data_nascimento: null,
              data_falecimento: null,
            },
            familiares: [],
            imoveis: [],
            veiculos: [],
            investimentos: [],
            participacoes_societarias: [],
            creditos: [],
            criptomoedas: [],
            outros_bens: [],
            seguros: [],
            bens_internacionais: [],
            dividas: []
          };
          const newInventory = {
            ...emptyInventory,
            ...data,
            id: databaseData.inventarios.length + 1,
          };
          databaseData.inventarios.push(newInventory);
          return { data: { status: true, message: "Inventory created successfully", inventory: newInventory } };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
      invalidatesTags: [{ type: 'Inventory' }]
    }),
    // getUsers: builder.query<User[], void>({
    //   async queryFn() {
    //     try {
    //       let users: User[] = [];
    //       data.users.forEach((user) => {
    //         users.push(user as User);
    //       });
    //       return { data: users };
    //     } catch (error: any) {
    //       console.error(error.message);
    //       return { error: error.message };
    //     }
    //   },
    // }),
    getUsersMe: builder.query<Usuario, void>({
      async queryFn() {
        try {
          if (!databaseData.usuarios || databaseData.usuarios.length === 0) {
            throw new Error('No user data available');
          }
          const user = databaseData.usuarios[0];
          if (!user) {
            throw new Error('User not found');
          }
          return { data: user as Usuario };
        } catch (error: any) {
          console.error('Failed to fetch user:', error.message);
          return { error: error.message || 'Failed to fetch user data' };
        }
      },
    }),
    getInventoriesFromUser: builder.query<Inventario[], ParamUserId>({
      async queryFn(queryArg) {
        try {
          let inventories: Inventario[] = databaseData.inventarios.filter(x => x.id_usuario == queryArg.userId) as Inventario[];
          return { data: inventories };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
    }),
    getInventoryById: builder.query<Inventario, ParamInventoryId>({
      async queryFn(queryArg) {
        try {
          let inventory: Inventario = databaseData.inventarios.find(x => x.id == queryArg.inventoryId) as Inventario;
          return { data: inventory };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
      providesTags: (result, error, { inventoryId }) => [
        { type: 'Inventory', id: inventoryId }
      ]
    }),
    deleteInventoryById: builder.mutation<PostResponse,ParamInventoryId>({
      async queryFn({ inventoryId }) {
        try {
          return { data: null };
        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
    }),
    updateInventory: builder.mutation<PostResponse, { inventoryId: number; data: Inventario, isArray: boolean, isItemToDelete: boolean, itemId: number }>({      
      async queryFn({ inventoryId, data, isArray, isItemToDelete, itemId }) {
        try {
          // Mock update implementation

          const inventoryIndex = databaseData.inventarios.findIndex(x => x.id === inventoryId);

          if (inventoryIndex !== -1) {

            if (!isArray) {
              // Update if property if isArray === false
              databaseData.inventarios[inventoryIndex] = { ...databaseData.inventarios[inventoryIndex], ...data };

            } else {
              // Handle array operations

              // Determine which property is an array in the data object
              const arrayPropertyKey = Object.keys(data).find(key => Array.isArray(data[key]));
              const inventory = {}
              inventory[arrayPropertyKey] = []
              
              const newItem = data[arrayPropertyKey][0]; // Assuming the new item is the first element

              // Item - Delete
              if (isItemToDelete) {
                const filteredItems = databaseData.inventarios[inventoryIndex][arrayPropertyKey].filter(item => item.id !== itemId);
                inventory[arrayPropertyKey] = filteredItems;
                databaseData.inventarios[inventoryIndex] = { ...databaseData.inventarios[inventoryIndex], ...inventory };
              }

              // Item - Include
              else if (itemId === 0) {
                newItem['id'] = databaseData.inventarios[inventoryIndex][arrayPropertyKey].length+1;
                inventory[arrayPropertyKey] = [...databaseData.inventarios[inventoryIndex][arrayPropertyKey], newItem];
                databaseData.inventarios[inventoryIndex] = { ...databaseData.inventarios[inventoryIndex], ...inventory };
                
              }
              
              // Item - Update
              else {
                newItem['id'] = itemId;
                const itemIndex = databaseData.inventarios[inventoryIndex][arrayPropertyKey].findIndex(item => item.id === itemId);
                if (itemIndex !== -1) {
                  inventory[arrayPropertyKey] = [...databaseData.inventarios[inventoryIndex][arrayPropertyKey]];
                  inventory[arrayPropertyKey][itemIndex] = newItem;
                  databaseData.inventarios[inventoryIndex] = { ...databaseData.inventarios[inventoryIndex], ...inventory };
                }
              }

            }
          }

          return { data: { status: true, message: "Inventory updated successfully" } };

        } catch (error: any) {
          console.error(error.message);
          return { error: error.message };
        }
      },
      invalidatesTags: (result, error, { inventoryId }) => [
        { type: 'Inventory', id: inventoryId }
      ]
    }),
  }),
});

export type ParamUserId = {
  userId: number;
};

export type ParamInventoryId = {
  inventoryId: number;
};

export type PostResponse = {
  status: boolean,
  message: string,
  inventory?: Inventario
}

export const { 
  useGetUsersMeQuery,
  useGetInventoriesFromUserQuery,
  useGetInventoryByIdQuery,
  useDeleteInventoryByIdMutation,
  useUpdateInventoryMutation,
  useCreateInventoryMutation
} = baseApi;